.TodoListItem {
  padding: 1rem;
  display: flex;
  align-items: center;
  background: #3f3f3f;

  border-radius: 20px;
  &:nth-child(even) {
    /*  background: #000000; */
  }
  .checkbox {
    cursor: pointer;
    flex: 1;
    display: flex;
    align-items: center;
    svg {
      font-size: 1.5rem;
      background-color: #3f3f3f;
    }
    .text {
      margin-left: 0.5rem;
      flex: 1;
      color: #000000;
      font-weight: bold;
    }

    &.checked {
      svg {
        color: #000000;
      }
      .text {
        color: #000000;
        text-decoration: line-through;
      }
    }
  }
  .remove {
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    color: #ff6b6b;
    cursor: pointer;
    &:hover {
      color: #ff8787;
    }
  }

  & + & {
    border-top: 0.1px solid #343434;
  }
}
