.TodoTemplate {
  width: 1000px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 6rem;
  border-radius: 4px;
  overflow: hidden;
}

.app-title {
  background: #313131;
  color: #000000;
  height: 4rem;
  font-size: 2rem;
  display: block;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  text-align-last: left;
}

.content {
  background: #313131;
}
