.TodoInsert {
  display: flex;
  background: #606060;
  border-radius: 15px;
}

input {
  background: none;
  height: 40px;
  outline: none;
  border: none;
  padding: 0.5rem;
  font-size: 1.125rem;
  line-height: 1.5;
  text-align: left;
  color: #000000;
  &::placeholder {
    color: #000000;
  }
  flex: 2 1;
}

button {
  background: none;
  outline: none;
  border: none;
  background: #606060;
  color: white;
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  border-radius: 0 15px 15px 0;
  border-bottom: 1px solid #606060;
  cursor: pointer;
  transition: 0.1s background ease-in;
  &:hover {
    background: #353535;
  }
}
